/*  */
html {
  color: #666;
  background: #fff;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
/* */
.movieImg {
  cursor: pointer;
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
}
.movieImg:hover {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
}
/*  */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  margin: 0;
  padding: 0;
}
/*  HTML5 , IE  js  createElement(TAG) */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
/* HTML5  img  */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}
/*  font  */
body,
button,
input,
select,
textarea {
  font: 12px/1.62 Helvetica, Arial, sans-serif;
}
input,
select,
textarea {
  outline: none;
  font-size: 100%;
}
/* Table  cell  */
table {
  border-spacing: 0;
  border-collapse: collapse;
}
/* IE bug fixed: th  text-align*/
th {
  text-align: inherit;
  font-weight: bold;
}
/*  */
fieldset,
img {
  border: 0;
}
/* ie6 7 8(q) bug  */
iframe {
  display: block;
}
/*  firefox  */
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
/*  del  */
del {
  text-decoration: line-through;
}
address,
caption,
cite,
code,
dfn,
em,
th,
var {
  font-weight: 500;
  font-style: normal;
}
/* , li  */
ol,
ul {
  list-style: none;
}
/* ,  */
caption,
th {
  text-align: left;
}
/* yahoo, ,  */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: 500;
}
q:before,
q:after {
  content: '';
}
/*  */
sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
/*   */
a:link,
a:visited {
  color: #08c;
}
/**/
a.redlink:link,
a.redlink:visited {
  color: #910004;
}
/*  */
a:hover {
  text-decoration: underline;
}
/*  */
ins,
a {
  text-decoration: none;
}
/*  */
code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}
/*  */
.clearfix:after {
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
  content: " ";
  font-size: 0;
}
.clearfix {
  zoom: 1;
  /* for IE6 IE7 */
}
/* ,  JS  */
.fn-hide {
  display: none;
}
/* , bug */
.fn-left,
.fn-right {
  display: inline;
}
.fn-left {
  float: left;
}
.fn-right {
  float: right;
}
/*  */
.fn-text-overflow {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
/*  */
.fn-linear {
  background: -webkit-gradient(linear, left top, left bottom, from(#fbfbfb), to(#f2f2f2));
  background: -webkit-linear-gradient(top, #fbfbfb, #f2f2f2);
  background: linear-gradient(top, #fbfbfb, #f2f2f2);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fbfbfb', endColorstr='#f2f2f2');
}
/*  */
.fn-linear-light {
  background: -webkit-gradient(linear, left top, left bottom, from(#fdfdfd), to(#eee));
  background: -webkit-linear-gradient(top, #fdfdfd, #eee);
  background: linear-gradient(top, #fdfdfd, #eee);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fdfdfd', endColorstr='#eee');
}
/* chrome  */
.fn-webkit-adjust {
  -webkit-text-size-adjust: none;
}
/* placeholder*/
::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #999;
}
:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999;
}
::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
}
:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #999;
}
/**/
.grid-950 {
  width: 950px;
  margin: 0 auto;
}
/*.container{
    padding-top: 10px;
    background: #f3f3f3;
    min-width: 950px;
}*/
/* fade,bounce*/
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.fade-enter-active,
.fade-leave-active {
  opacity: 0;
}
.bounce-enter-active {
  -webkit-animation: bounce-in 0.5s;
          animation: bounce-in 0.5s;
}
.bounce-leave-active {
  -webkit-animation: bounce-out 0.5s;
          animation: bounce-out 0.5s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
.container {
  float: left;
  width: 590px;
}
.right-side {
  float: right;
  width: 310px;
}
.two-list {
  min-height: 500px;
}
.tit {
  margin-top: 20px;
}
.tit h1 {
  display: inline-block;
  width: 90px;
  font-size: 20px;
  color: #000;
}
.tit .locat {
  position: relative;
  display: inline-block;
}
.locat .cities-list {
  position: absolute;
  left: 0;
  top: 18px;
  padding: 10px;
  background: #fff;
  z-index: 999;
  border: 1px solid #ccc;
}
.locat .cities-list span {
  display: block;
}
.tab-hd {
  vertical-align: bottom;
}
.tab-hd li {
  margin: 0 5px;
  float: left;
}
.tab-hd .on {
  background-color: #69c;
  color: #fff;
  padding: 0 10px;
  border-radius: 2px;
}
.hd {
  padding: 10px 0;
  border-bottom: 1px dashed #ccc;
}
.hd h2,
.hd .tab-hd {
  display: inline-block;
}
.load-more {
  text-align: center;
}



